<template>
    <div class="bigBox" id="bigBox">
        <div class="boxHeader">
            <button @click="fullScreen()">全屏</button>
            <button @click="fullExit()">退出全屏</button>
        </div>
        <div class="box_main">
            <div class="main_left">
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">1AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)" class="bgm_box1">
                            <div class="bgm_left" ref="barOne"></div>
                            <div class="bgm_right"></div>
                        </Bgm>
                    </el-col>
                </div>
                
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">2AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)">
                            
                        </Bgm>
                    </el-col>
                </div>
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">3AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)">
                            
                        </Bgm>
                    </el-col>
                </div>
            </div>
            <div class="main_center">
            
            </div>
            <div class="main_right">
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">4AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)" class="bgmBox4">
                            <div class="barFour" ref="barFour"></div>
                        </Bgm>
                    </el-col>
                </div>
                
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">5AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)">
                            
                        </Bgm>
                    </el-col>
                </div>
                <div class="rowBox">
                    <el-col :span="24" class="elCol">
                        <div class="bgmTitle">6AVG状态</div>
                        <Bgm style="height:calc(100% - 3rem)">
                            
                        </Bgm>
                    </el-col>
                </div>
            </div>
            <!-- 通过gutter控制 el-col之间的 栅格间隔 -->
            <!-- 通过margin-top 给el-row 套个盒子 控制上下之间的距离-->
            <!-- <el-row :gutter="10">
                <el-col :span="12" class="elCol">
                    <div class="bgmTitle">1AVG状态</div>
                    <Bgm style="height:calc(100% - 3rem)">
                        
                    </Bgm>
                </el-col>
                <el-col :span="12" class="elCol">
                    <div class="bgmTitle">2AVG状态</div>
                    <Bgm style="height:calc(100% - 3rem)">
                        
                    </Bgm>
                </el-col>
                
            </el-row> -->
            
            
        </div>
    </div>
</template>

<script>
import Bgm from "@/components/Bgm/cssBgm";
import * as echarts from 'echarts';
// 
// import {barOneOption} from "@/views/project/echartsjs/echartlayouttwo";
import barOneOption from "@/views/project/echartsjs/echartlayouttwo2";
export default {
    name:'echartlayouttwo',
    components:{
        Bgm
    },
    data() {
        return {
            
        }
    },
    mounted(){
        this.barEchartsApi()
        this.barEchartsFn4()
    },
    methods:{
        // 接口 第一个echarts图
        barEchartsApi(){
            // 接口有数据时,渲染整个options数据
            if(1){
                this.barEchartsFn()
            }else{
                // 接口没数据时,渲染options 暂无数据数据

            }

        },
        // 第一个echarts图
        barEchartsFn(){
            //初始化 饼图
            let barCharts = echarts.init(this.$refs.barOne);
            //监听窗口大小，发生改变 监听浏览器大小事件window.onresize = function () {}
            //   resize()是echarts中的大小事件变化方法
            window.onresize = function () {
                barCharts.resize();
            };
            //   修改echarts值
            let xAxis = barOneOption.xAxis;
            let series = barOneOption.series;
            // 接口中的数据
            // xAxis.data = categoryData;
            // series[0].data = linedata;

            //   合并属性
            let newOption = Object.assign({}, barOneOption, { xAxis }, { series });
            //调用setOption设置选项
            barCharts.setOption(newOption);
        },

        // 第四个饼图
        barEchartsFn4(){

            let optionData= [{
                    name: '林地面积统计',
                    value: 10000,
                    itemStyle: {
                        color: '#22c4ff',
                    }
                }, {
                    name: '草地面积统计',
                    value: 12116,
                    itemStyle: {
                        color: '#aaff00'
                    }
                }, {
                    name: '耕地地面积统计',
                    value: 16616,
                    itemStyle: {
                        color: '#ffaaff'
                    }
                }]

            //初始化 饼图
            let myChart = echarts.init(this.$refs.barFour);
            
            
            // 传入数据生成 option
            let option = this.getPie3D(optionData, 0.8);
            myChart.setOption(option);
            //是否需要label指引线，如果要就添加一个透明的2d饼状图并调整角度使得labelLine和3d的饼状图对齐，并再次setOption
            this.option.series.push({
                name: 'pie2d',
                type: 'pie',
                labelLine:{
                    length:10,
                    length2:10
                },
                startAngle: -20 , //起始角度，支持范围[0, 360]。
                clockwise: false,//饼图的扇区是否是顺时针排布。上述这两项配置主要是为了对齐3d的样式
                radius: ['20%', '50%'],
                center: ['50%', '50%'],
                data: optionData,
                itemStyle:{
                    opacity:0
                }
            });
            myChart.setOption(option);

            //监听窗口大小，发生改变 监听浏览器大小事件window.onresize = function () {}
            //   resize()是echarts中的大小事件变化方法
            window.onresize = function () {
                myChart.resize();
            };
        },
        getPie3D(pieData, internalDiameterRatio) {
            //internalDiameterRatio:透明的空心占比
            let that = this;
            let series = [];
            let sumValue = 0;
            let startValue = 0;
            let endValue = 0;
            let legendData = [];
            let legendBfb = [];
            let k = 1 - internalDiameterRatio;
            pieData.sort((a, b) => {
                return (b.value - a.value);
            });
            // 为每一个饼图数据，生成一个 series-surface 配置
            for (let i = 0; i < pieData.length; i++) {
                sumValue += pieData[i].value;
                let seriesItem = {
                    name: typeof pieData[i].name === 'undefined' ? `series${i}` : pieData[i].name,
                    type: 'surface',
                    parametric: true,
                    wireframe: {
                        show: false
                    },
                    pieData: pieData[i],
                    pieStatus: {
                        selected: false,
                        hovered: false,
                        k: k
                    },
                    center: ['10%', '50%']
                };

                if (typeof pieData[i].itemStyle != 'undefined') {
                    let itemStyle = {};
                    typeof pieData[i].itemStyle.color != 'undefined' ? itemStyle.color = pieData[i].itemStyle.color : null;
                    typeof pieData[i].itemStyle.opacity != 'undefined' ? itemStyle.opacity = pieData[i].itemStyle.opacity : null;
                    seriesItem.itemStyle = itemStyle;
                }
                series.push(seriesItem);
            }

            // 使用上一次遍历时，计算出的数据和 sumValue，调用 getParametricEquation 函数，
            // 向每个 series-surface 传入不同的参数方程 series-surface.parametricEquation，也就是实现每一个扇形。
            legendData = [];
            legendBfb = [];
            for (let i = 0; i < series.length; i++) {
                endValue = startValue + series[i].pieData.value;
                series[i].pieData.startRatio = startValue / sumValue;
                series[i].pieData.endRatio = endValue / sumValue;
                series[i].parametricEquation = this.getParametricEquation(series[i].pieData.startRatio, series[i].pieData.endRatio,
                    false, false, k, series[i].pieData.value);
                startValue = endValue;
                let bfb = that.fomatFloat(series[i].pieData.value / sumValue, 4);
                legendData.push({
                    name: series[i].name,
                    value: bfb
                });
                legendBfb.push({
                    name: series[i].name,
                    value: bfb
                });
            }
            let boxHeight = this.getHeight3D(series, 26);//通过传参设定3d饼/环的高度，26代表26px
            // 准备待返回的配置项，把准备好的 legendData、series 传入。
            let option = {
                legend: {
                    data: legendData,
                    orient: 'horizontal',
                    left: 10,
                    top: 10,
                    itemGap: 10,
                    textStyle: {
                        color: '#A1E2FF',
                    },
                    show: true,
                    icon: "circle",
                    formatter: function (param) {
                        let item = legendBfb.filter(item => item.name == param)[0];
                        let bfs = that.fomatFloat(item.value * 100, 2) + "%";
                        return `${item.name}  ${bfs}`;
                    }
                },
                labelLine: {
                    show: true,
                    lineStyle: {
                        color: '#7BC0CB'
                    }
                },
                label: {
                    show: true,
                    position: 'outside',
                    rich: {
                        b: {
                            color: '#7BC0CB',
                            fontSize: 12,
                            lineHeight: 20
                        },
                        c: {
                            fontSize: 16,
                        },
                    },
                    formatter: '{b|{b} \n}{c|{c}}{b|  亩}',

                },
                tooltip: {
                    formatter: params => {
                        if (params.seriesName !== 'mouseoutSeries' && params.seriesName !== 'pie2d') {
                            let bfb = ((option.series[params.seriesIndex].pieData.endRatio - option.series[params.seriesIndex].pieData.startRatio) *
                                100).toFixed(2);
                            return `${params.seriesName}<br/>` +
                                `<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:${params.color};"></span>` +
                                `${bfb}%`;
                        }
                    }
                },
                xAxis3D: {
                    min: -1,
                    max: 1
                },
                yAxis3D: {
                    min: -1,
                    max: 1
                },
                zAxis3D: {
                    min: -1,
                    max: 1
                },
                grid3D: {
                    show: false,
                    boxHeight: boxHeight, //圆环的高度
                    viewControl: { //3d效果可以放大、旋转等，请自己去查看官方配置
                        alpha: 40, //角度
                        distance: 300,//调整视角到主体的距离，类似调整zoom
                        rotateSensitivity: 0, //设置为0无法旋转
                        zoomSensitivity: 0, //设置为0无法缩放
                        panSensitivity: 0, //设置为0无法平移
                        autoRotate: false //自动旋转
                    }
                },
                series: series
            };
            return option;
        },

        //获取3d丙图的最高扇区的高度
        getHeight3D(series, height) {
            series.sort((a, b) => {
                return (b.pieData.value - a.pieData.value);
            })
            return height * 25 / series[0].pieData.value;
        },

        // 生成扇形的曲面参数方程，用于 series-surface.parametricEquation
        getParametricEquation(startRatio, endRatio, isSelected, isHovered, k, h) {
            // 计算
            let midRatio = (startRatio + endRatio) / 2;
            let startRadian = startRatio * Math.PI * 2;
            let endRadian = endRatio * Math.PI * 2;
            let midRadian = midRatio * Math.PI * 2;
            // 如果只有一个扇形，则不实现选中效果。
            if (startRatio === 0 && endRatio === 1) {
                isSelected = false;
            }
            // 通过扇形内径/外径的值，换算出辅助参数 k（默认值 1/3）
            k = typeof k !== 'undefined' ? k : 1 / 3;
            // 计算选中效果分别在 x 轴、y 轴方向上的位移（未选中，则位移均为 0）
            let offsetX = isSelected ? Math.cos(midRadian) * 0.1 : 0;
            let offsetY = isSelected ? Math.sin(midRadian) * 0.1 : 0;
            // 计算高亮效果的放大比例（未高亮，则比例为 1）
            let hoverRate = isHovered ? 1.05 : 1;
            // 返回曲面参数方程
            return {
                u: {
                    min: -Math.PI,
                    max: Math.PI * 3,
                    step: Math.PI / 32
                },
                v: {
                    min: 0,
                    max: Math.PI * 2,
                    step: Math.PI / 20
                },
                x: function (u, v) {
                    if (u < startRadian) {
                        return offsetX + Math.cos(startRadian) * (1 + Math.cos(v) * k) * hoverRate;
                    }
                    if (u > endRadian) {
                        return offsetX + Math.cos(endRadian) * (1 + Math.cos(v) * k) * hoverRate;
                    }
                    return offsetX + Math.cos(u) * (1 + Math.cos(v) * k) * hoverRate;
                },
                y: function (u, v) {
                    if (u < startRadian) {
                        return offsetY + Math.sin(startRadian) * (1 + Math.cos(v) * k) * hoverRate;
                    }
                    if (u > endRadian) {
                        return offsetY + Math.sin(endRadian) * (1 + Math.cos(v) * k) * hoverRate;
                    }
                    return offsetY + Math.sin(u) * (1 + Math.cos(v) * k) * hoverRate;
                },
                z: function (u, v) {
                    if (u < -Math.PI * 0.5) {
                        return Math.sin(u);
                    }
                    if (u > Math.PI * 2.5) {
                        return Math.sin(u) * h * .1;
                    }
                    return Math.sin(v) > 0 ? 1 * h * .1 : -1;
                }
            };
        },

        fomatFloat(num, n) {
            var f = parseFloat(num);
            if (isNaN(f)) {
                return false;
            }
            f = Math.round(num * Math.pow(10, n)) / Math.pow(10, n); // n 幂   
            var s = f.toString();
            var rs = s.indexOf('.');
            //判定如果是整数，增加小数点再补0
            if (rs < 0) {
                rs = s.length;
                s += '.';
            }
            while (s.length <= rs + n) {
                s += '0';
            }
            return s;
        },


        // 全屏
        fullScreen() {
            //若要全屏页面中div，
            var element= document.getElementById("bigBox");  

            // document.documentElement这是html整个页面的节点
            // var element = document.documentElement; 
            //IE 10及以下ActiveXObject  
            if (window.ActiveXObject) {
                var WsShell = new ActiveXObject('WScript.Shell')
                WsShell.SendKeys('{F11}');
            }
            //HTML W3C 提议  
            else if (element.requestFullScreen) {
                element.requestFullScreen();
            }
            //IE11  
            else if (element.msRequestFullscreen) {
                element.msRequestFullscreen();
            }
            // Webkit (works in Safari5.1 and Chrome 15)  
            else if (element.webkitRequestFullScreen) {
                element.webkitRequestFullScreen();
            }
            // Firefox (works in nightly)  
            else if (element.mozRequestFullScreen) {
                element.mozRequestFullScreen();
            }
        },
        //退出全屏  
        fullExit() {
            //若要全屏页面中div，
            var element= document.getElementById("bigBox");   
            // var element = document.documentElement; 
            //IE ActiveXObject  
            if (window.ActiveXObject) {
                var WsShell = new ActiveXObject('WScript.Shell')
                WsShell.SendKeys('{F11}');
            }
            //HTML5 W3C 提议  
            else if (element.requestFullScreen) {
                document.exitFullscreen();
            }
            //IE 11  
            else if (element.msRequestFullscreen) {
                document.msExitFullscreen();
            }
            // Webkit (works in Safari5.1 and Chrome 15)  
            else if (element.webkitRequestFullScreen) {
                document.webkitCancelFullScreen();
            }
            // Firefox (works in nightly)  
            else if (element.mozRequestFullScreen) {
                document.mozCancelFullScreen();
            }
        }
    }
}
</script>

<style lang="less" scoped>
.bigBox {
    height: 100%;
    // padding: 1rem 1.6rem;
    .boxHeader{
        height: 10%;
        // background-color: pink;
    }
    .box_main{
        height: 90%;
        // background-color: aquamarine;
        display: flex;
        .main_left{
            height: 100%;
            width: 25%;
            .bgm_box1{
                display: flex;
                .bgm_left{
                    height: 100%;
                    width: 55%;
                    // background-color: pink;
                }
                .bgm_right{
                    height: 100%;
                    width: 45%;
                    // background-color: pink;
                }
            }
        }
        .main_center{
            height: 100%;
            width: 50%;
        }
        .main_right{
            height: 100%;
            width: 25%;
            .bgmBox4{
                .barFour{
                    width: 100%;
                    height: 100%;
                    // background-color: gray;
                }
            }
        }
        // 共同样式
        .rowBox{
            margin-top: 1rem;
            height: calc(100% / 3 - 1rem);
            .elCol{
                height: 100%;
            }
        }
        .bgmTitle{
            height: 3rem;
            background-color: #06246a;
        }
        
    }
}

</style>