// 第一个图
import * as echarts from 'echarts'
import 'echarts-gl'
let colors = [
    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
        {
            offset: 0,
            color: "#0CEE92",
        },
        {
            offset: 1,
            color: "#06246E",
        },
    ]),
    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
        {
            offset: 0,
            color: "#06246E",
        },
        {
            offset: 1,
            color: "#D7B352",
        },
    ]),
    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
        {
            offset: 0,
            color: "#06246E",
        },
        {
            offset: 1,
            color: "#FC3E3F",
        },
    ]),
    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
        {
            offset: 0,
            color: "#06246E",
        },
        {
            offset: 1,
            color: "#AACCFF",
        },
    ]),
    new echarts.graphic.LinearGradient(0, 1, 0, 0, [
        {
            offset: 1,
            color: "#00B3FF",
        },
        {
            offset: 0,
            color: "#06246E",
        },
    ]),
];
let total_count=99

